import { RSAA } from 'redux-api-middleware';
import { API_ROOT } from 'Core/constants';
import { 
  SIGN_UP_REQUEST,
  SIGN_UP_SUCCESS,
  SIGN_UP_FAILURE,
} from '../constants';

export function postSignupAction(signupObject) {
  return {
      [RSAA]: {
          endpoint: `${API_ROOT}/customer/register`,
          method: 'POST',
          body: JSON.stringify(signupObject),
          types: [
            SIGN_UP_REQUEST,
            SIGN_UP_SUCCESS,
            SIGN_UP_FAILURE,
          ]
      }
  }
}
